#hamMenu{
  background: #b92b27;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #1565C0, #b92b27);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #1565C0, #b92b27); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
body{
  height: 100%;
  width: 100%;
  background: #252121;  /* fallback for old browsers */
}
.landingWrapper{
  color: white;
  -webkit-backface-visibility: hidden;
  overflow: hidden;
  height: 100vh;
  width: 100%;
  margin:auto;
  text-align: center;
  background: #b92b27;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #1565C0, #b92b27);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #1565C0, #b92b27); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.link::after{
  content: '';
  display: block;
  position: relative;
  top: -15px;
  width: 0;
  height: 2px;
  background-color: white;
  transition: width .3s;
}

.link:hover::after{
  width: 100%;
  transition: width .3s;
}

div.mdl-layout__drawer-button{
    display: none;
  }
@media screen and (max-width: 1024px){
  #hamMenu {
    display: block;
  }
  div.mdl-layout__drawer-button{
    color: white;
  }
}

@media screen and (max-width: 780px){
  .mdl-layout__header-row .mdl-navigation {
    display: none;
  }
  .mdl-layout__header-row > *{
    width: 100%;
    text-align: right;
  }
  span.mdl-layout__title, span.mdl-layout-title{
    font-size: 15px;
  }
  div.mdl-layout__drawer-button{
    display: block;
  }
}